html, body {
  margin: 0;
  padding: 0;
  color: #1b1b1b;
  font-family: 'Bowlby One SC', cursive;
  font-size: 16px;
  /* Remove tap side effects on mobile devices (iOS) */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  
}

@media screen and (max-width: 500px) {
  html, body {
    font-size: 12px;
  }
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 1rem 2rem;
  background-color: transparent;
  text-align: center;
}

header h1 {
  position: relative;
  margin: 0;
  color: #222;
  font-weight: normal;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
main {
  width: 75vw;
  margin: 0 auto;
}
.container {
  box-sizing: border-box;
  height: 100vh;
  padding: 5.3rem 0 4rem;
}

.three-root {
  position: relative;
  height: 100%;
  background-color: #fff;
  -webkit-transition: .2s ease-out 1.2s;
  transition: .2s ease-out 1.2s;
}

.three-root::before,
.three-root::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 20%;
  height: 40%;
  border-width: 0;
  border-style: solid;
  -webkit-transition: .2s ease-out 1s;
  transition: .2s ease-out 1s;
}

.three-root::before {
  top: -1.1rem;
  left: -1.1rem;
  -webkit-border-image: -webkit-linear-gradient(left, #27dafa, #2580e7) 30 30;
          border-image: linear-gradient(to right, #27dafa, #2580e7) 30 30;
}

.three-root::after {
  right: -1.1rem;
  bottom: -1.1rem;
  -webkit-border-image: -webkit-linear-gradient(right, #27dafa, #2580e7) 30 30;
          border-image: linear-gradient(to left, #27dafa, #2580e7) 30 30;
}

.three-root:hover::before,
.three-root:hover::after {
  border-width: .8rem;
}

.three-root:hover {
  box-shadow: 0px 0px 0px .5rem #1b1b1b;
}

/* Disable canvas selection */
.container canvas {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
footer {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem 0;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

footer a {
  position: relative;
  color: #1b1b1b;
  text-decoration: none;
  cursor: pointer;
}

footer a::before {
  content: '';
  position: absolute;
  right: -3px;
  bottom: 0;
  left: -3px;
  height: 0;
  z-index: -1;
  -webkit-transition: height .2s ease;
  transition: height .2s ease;
}

footer a:hover::before {
  height: 45%;
  background-image: -webkit-linear-gradient(left, #27dafa, #2580e7);
  background-image: linear-gradient(to right, #27dafa, #2580e7);
}

footer span {
  margin: 3px;
}
